.side-navbar{
     width: 350px;
     height: 90vh;
     background: #483eff;
     border-radius: 15px;
     overflow: hidden;
     padding: 30px;
     color: #f1f1f1;
     text-transform: uppercase;
     margin: 0 150px;
}
.step{
     display: grid;
     grid-template-columns: 40% 60%;
     align-items: center;
     width: 90%;
     margin: 15px 0;
}
button{
     width: 50px;
     height: 50px;
     border-radius: 50%;
     border: 2px solid #f1f1f1;
     background: transparent;
     color: #f1f1f1;
     font-size: 18px;
     font-weight: 700;
     cursor: pointer;
     transition: all 0.3s ease-out;
}
.active-btn{
     background: #bce1ff;
     color: #333;
     border: none;
}
.title-group p{
     margin: 5px 0;
     opacity: 0.8;
}
.title-group h2{
     margin: 5px 0;
     font-size: 20px;
}

@media screen and (max-width: 1150px) {
     .side-navbar{
          width: 80vw;
          height: 350px;
          margin: 30px auto;
     }
}
@media screen and (max-width: 520px) {
     .step{
          grid-template-columns: 25% 75%;
     }
}
@media screen and (max-width: 450px){
     button{
          width: 40px;
          height: 40px;
     }
     .title-group p{
          font-size: 12px
     }
     .title-group h2{
          font-size: 15px;
     }
}